.footer{
    background-image: url(../images/footer-mob.jpeg);
           
            background-repeat: no-repeat;
            background-size: cover;
  
    @media screen and (min-width: 768px) and (max-width: 1279px){
        background-image: url(../images/footer-tab.jpeg);
        background-position: center;
        background-repeat: no-repeat;
        
        height: 506px;
    }
    @media screen and (min-width: 1280px){
    background-image: url(../images/footer.jpeg);
    background-position: bottom;
    background-repeat: no-repeat;
    height: 395px;
}}
.footer__wrap{
    display: flex;
   
}

.logo__contain{
    @media screen and (max-width: 767px){
        display: block;
        flex-direction: column;
        padding-top: 60px;
    }
    @media screen and (min-width: 768px) and (max-width: 1279px){
        display: flex;
        flex-wrap: wrap;
        padding-top: 100px;
        flex-direction: column;
    }
   
    @media screen and (min-width: 1280px){
    display: flex;
    padding-top: 100px;
    justify-content: space-between;
    
    
}}
.logo__link{
    width: 66px;
}
.list{
    &__item{
        font-weight: 400;
font-size: 14px;
line-height: 16px;
color: #2C2C2C;
    }
  
    @media screen and (max-width: 767px){
      margin-left: 81px;
        &__link{
            padding-top: 18px;

        }
        
    }
    @media screen and (min-width: 768px) and (max-width: 1279px){
    display: flex;

    margin-left: 114px;
  
    &__link{
        list-style: none;
        margin-right: 28px;
        padding-top: 18px;

        &:last-child{
            margin-right: 0;
        }
    }
  }

    @media screen and (min-width: 1280px){
    display: flex;
    margin-left: 148px;
   
    &__link{
        list-style: none;
        margin-right: 28px;
        padding-top: 18px;

        &:last-child{
            margin-right: 0;
        }
    }

}}

.footer__subscribe{
    
    &--text{
       
        margin-bottom: 12px;
font-style: normal;
font-weight: 500;
font-size: 14px;
line-height: 150%;
color: #2C2C2C;
    }
    &--title{
        font-size: 12px;
line-height: 14px;
color: #2C2C2C
    }
    &--item{
        color: #7EB101;
        text-decoration: underline;
    }
    &--btn{
        width: 103px;
height: 42px;
background: #FFFFFF;
border: 0.854167px solid #7EB101;
border-radius: 30px;
margin-left: 13px;
    }
    &--input{
        width: 279px;
        height: 42px;
        border: 0.854167px solid #FFE550;
        border-radius: 30px;
        margin-bottom: 12px;
        padding:0px 0px 0px 10px;
      
        &::placeholder{
            font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    color: #505050;
    padding: 12px 12px 12px 12px;
    
    align-items: center;
        }
    }
    @media screen and (max-width: 767px){
        &--title{
            margin-bottom: 60px;
        }
        &--btn{
            margin-left: 175px;
            margin-bottom: 20px;
        }
        
    }
    @media screen and (min-width: 768px) and (max-width: 1279px){
        margin-top: 40px;
       
    }
  
